import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { TipoContenidoModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/tipo-contenido.model';
import { VentanillaUnicaContenidoModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-contenido.model';
import { VentanillaUnicaItemModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-item.model';
import { VentanillaUnicaService } from 'src/app/commons/services/app-terrae-services/ventanilla-unica.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

@Component({
  selector: 'app-ventanilla-unica-component-new',
  templateUrl: './ventanilla-unica-contenido-new.component.html',
  styleUrls: ['./ventanilla-unica-contenido-new.component.css']
})
export class VentanillaUnicaComponentNewComponent implements OnInit {

  // ***** CUADRO INTRODUCIR TEXTO ***** //
  editor = ClassicEditor;

  // ***** FORMULARIO ***** //
  public formContenido: FormGroup;
  private urlRegex = /^(https?:\/\/)?[\w-]+(\.[\w-]+)+\.?(:\d+)?(\/\S*)?$/gi;
  private mailRegex = /^(?=.{1,254}$)(?=.{1,64}@)[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+(\.[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+)*@[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?(\.[A-Za-z]{2,})+$/;
  private telRegex = /^[0-9]+$/;

  // ***** VARIABLES DEL FORMULARIO ***** //
  public listaTipos: TipoContenidoModel[] = [];
  public tipoContenido: TipoContenidoModel;
  public listadoItems: VentanillaUnicaItemModel[] = [];

  // ***** VARIABLES NUEVO CONTENIDO ***** //
  public data: string;
  public titulo: string;
  public subtitulo: string;
  public tipo: TipoContenidoModel;
  public item: VentanillaUnicaItemModel;
  public activo: boolean;

  // ***** INTERFAZ ***** //
  @BlockUI('window') blockUI: NgBlockUI;


  constructor(
    private ventanillaUnicaService: VentanillaUnicaService,
    private router: Router,
    private utilAlert: UtilAlert,
  ) { }

  ngOnInit() {

    this.listarTipoContenido();
    this.listarItemsActivos();
    this.iniciarFormulario();

  }

  // ***** FUNCIONES DE ARRANQUE ***** //
  private listarTipoContenido(): void {
    this.ventanillaUnicaService.findAllTipoContenido().subscribe(result => {
      this.listaTipos = result;
    })
  }

  private listarItemsActivos(): void {
    this.ventanillaUnicaService.findAllActivo().subscribe(result => {
      this.listadoItems = result;
    })
  }

  private iniciarFormulario(): void {

    const textDefault = "Comienza a escribir tu texto en este lugar...";

    this.formContenido = new FormGroup({
      data: new FormControl(textDefault, [Validators.required]),
      titulo: new FormControl("", [Validators.required]),
      subtitulo: new FormControl(""),
      tipo: new FormControl("", [Validators.required]),
      item: new FormControl("", [Validators.required]),
      activo: new FormControl(true, [Validators.required]),
      url: new FormControl("", [Validators.required, Validators.pattern(this.urlRegex)]),
      email: new FormControl("", [Validators.required, Validators.pattern(this.mailRegex)]),
      tel: new FormControl("", [Validators.required, Validators.pattern(this.telRegex), Validators.minLength(9), Validators.maxLength(9)])
    })
  }


  // & Cambiamos el tipo de contenido
  public cambiarTipoContenido(tipo: TipoContenidoModel) {
    this.tipoContenido = tipo;
    if (this.tipoContenido.tipo === 'Texto') {
      this.desactivarData();
      this.formContenido.get('data').setValidators([Validators.required]);
    } else if (this.tipoContenido.tipo === 'Correo') {
      this.desactivarData();
      this.formContenido.get('email').setValidators([Validators.required, Validators.pattern(this.mailRegex)]);
    } else if (this.tipoContenido.tipo === 'Telfono') {
      this.desactivarData();
      this.formContenido.get('tel').setValidators([Validators.required, Validators.pattern(this.telRegex), Validators.minLength(9), Validators.maxLength(9)]);
    } else {
      this.desactivarData();
      this.formContenido.get('url').setValidators([Validators.required, Validators.pattern(this.urlRegex)]);
    }
    this.actualizarValidaciones();
  }

  // & deshabilitar campos de data
  private desactivarData(): void {
    this.formContenido.get('data').setValidators([]);
    this.formContenido.get('url').setValidators([]);
    this.formContenido.get('email').setValidators([]);
    this.formContenido.get('tel').setValidators([]);
  }

  // & actualizar cambios en validaciones
  private actualizarValidaciones(): void {
    this.formContenido.get('data').updateValueAndValidity();
    this.formContenido.get('url').updateValueAndValidity();
    this.formContenido.get('tel').updateValueAndValidity();
    this.formContenido.get('email').updateValueAndValidity();
  }


  // ***** FUNCIONES DE BOTONES Y SELECTORES ***** //
  // & Crear objeto contenido
  private crearContenido(): VentanillaUnicaContenidoModel {
    this.titulo = this.formContenido.get("titulo").value;
    this.subtitulo = this.formContenido.get("subtitulo").value;
    this.tipo = this.formContenido.get("tipo").value;
    if (this.tipo.tipo == 'Texto') {
      this.data = this.formContenido.get("data").value;
    } else if (this.tipo.tipo == 'Correo') {
      this.data = this.formContenido.get("email").value;
    } else if (this.tipo.tipo == 'Telfono') {
      this.data = "+56" + this.formContenido.get('tel').value;
    } else {
      this.data = this.formContenido.get("url").value;
    }
    this.item = this.formContenido.get("item").value;
    this.activo = this.formContenido.get("activo").value;

    const ventanillaUnicaContenidoModel = new VentanillaUnicaContenidoModel({
      data: this.data,
      titulo: this.titulo,
      subtitulo: this.subtitulo,
      tipo: this.tipo,
      item: this.item,
      activo: this.activo,
      orden: 0,
      id: 0
    });

    return ventanillaUnicaContenidoModel;

  }

  // & Guardar contenido
  public saveContenido() {

    if (this.formContenido.valid) {
      this.blockUI.start("Guardando ventanilla...");
      this.ventanillaUnicaService.saveContenido(this.crearContenido()).subscribe(result => {
        this.router.navigateByUrl('/app-terrae/ventanilla-unica');
        this.utilAlert.successSwal("Se ha creado el contenido N " + result.id + " (" + result.titulo + ")");
        this.blockUI.stop();
      }, (_error) => {
        this.blockUI.stop();
        this.utilAlert.toastError(_error?.error?.message)
      })
    } else {
      this.utilAlert.toastError("No se puede guardar la ventanilla, asegrese de que todos los campos obligatorios estn completos");
      this.blockUI.stop();
    }
  }

  // & Volvemos al men anterior
  public volver(): void {
    this.router.navigate([`app-terrae/ventanilla-unica`])
  }

}
