import { TipoContenidoModel } from "./tipo-contenido.model";
import { VentanillaUnicaItemModel } from "./ventanilla-unica-item.model";

export class VentanillaUnicaContenidoModel {

    public id: number = 0;
    public data: string = "";
    public titulo: string = "";
    public subtitulo: string = "";
    public tipo: TipoContenidoModel;
    public item: VentanillaUnicaItemModel;
    public activo: boolean = false;
    public orden: number = 0;
    public icono: string = "";

    constructor(fields?: {
        id?: number,
        data?: string,
        titulo?: string,
        subtitulo?: string,
        tipo?: TipoContenidoModel,
        item?: VentanillaUnicaItemModel,
        activo?: boolean,
        orden?: number,
        icono?: string
    }) {
        if (fields) { Object.assign(this, fields); }
    }


}