import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VentanillaUnicaItemModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-item.model';
import { VentanillaUnicaService } from 'src/app/commons/services/app-terrae-services/ventanilla-unica.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

@Component({
  selector: 'app-ventanilla-unica-new',
  templateUrl: './ventanilla-unica-new.component.html',
  styleUrls: ['./ventanilla-unica-new.component.css']
})

export class VentanillaUnicaNewComponent implements OnInit {

  // ***** Block UI ***** //
  @BlockUI('window') blockUI: NgBlockUI;

  // ***** FORMULARIOS ***** //
  public formItem: FormGroup;


  // ***** VARIABLES DEL FORMULARIO ***** //
  public listadoItems: VentanillaUnicaItemModel[] = [];
  private ningunoItem: VentanillaUnicaItemModel = { // Este es para la opcin de vaco
    id: null,
    item: "Ninguno",
    padre: null,
    activo: null,
    orden: null,
    numHijos: null,
    numContenidos: null
  };

  // ***** VARIABLES ITEM ***** //
  private item: string = "";
  private activo: boolean = false;
  private padre: VentanillaUnicaItemModel;

  ngOnInit() {
    this.listarItemsActivos();
    this.iniciarFormulario();
  }

  constructor(
    private ventanillaUnicaService: VentanillaUnicaService,
    private router: Router,
    private utilAlert: UtilAlert,
  ) { }


  // ***** INICIO ***** //
  private listarItemsActivos(): void {
    this.ventanillaUnicaService.findAllActivo().subscribe(result => {
      this.listadoItems = result;
      this.listadoItems.unshift(this.ningunoItem);

    })
  }

  private iniciarFormulario(): void {
    this.formItem = new FormGroup({
      item: new FormControl("", [Validators.required]),
      activo: new FormControl(true, [Validators.required]),
      padre: new FormControl("", [Validators.required]),
    })
  }

  // ***** GUARDAR ITEM ***** //

  // & Creamos objeto item
  private crearItem(): VentanillaUnicaItemModel {
    this.item = this.formItem.get("item").value;
    this.activo = this.formItem.get("activo").value;
    let padreTemp = this.formItem.get("padre").value;
    if (padreTemp.item == 'Ninguno') {
      this.padre = null;
    } else {
      this.padre = this.formItem.get("padre").value;
    }

    const ventanillaUnicaItemModel = new VentanillaUnicaItemModel({
      item: this.item,
      activo: this.activo,
      padre: this.padre,
      orden: 0,
      id: 0
    });

    return ventanillaUnicaItemModel;

  }

  // & Guardar item
  public saveItem() {
    if (this.formItem.valid) {
      this.blockUI.start("Guardando ventanilla...");
      this.ventanillaUnicaService.save(this.crearItem()).subscribe(result => {
        this.router.navigateByUrl('/app-terrae/ventanilla-unica');
        this.utilAlert.successSwal("Se ha creado la ventanilla N " + result.id + " (" + result.item + ")");
        this.blockUI.stop();
      }, (_error) => {
        this.blockUI.stop();
        this.utilAlert.toastError(_error?.error?.message)
      })
    } else {
      this.utilAlert.toastError("Ha ocurrido un error al momento de crear la ventanilla.");
      this.blockUI.stop();
    }
  }

  // & Volvemos al men anterior
  public volver(): void {
    this.router.navigate([`app-terrae/ventanilla-unica`])
  }

}



