import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment";
import { IPaginacion } from "../../interfaces/paginacion.interface";
import { VentanillaUnicaItemModel } from "../../models/app-terrae/ventanilla-unica/ventanilla-unica-item.model";
import { HttpClient } from "@angular/common/http";
import { VentanillaUnicaFullModel } from "../../models/app-terrae/ventanilla-unica/ventanilla-unica-full.model";
import { VentanillaUnicaContenidoModel } from "../../models/app-terrae/ventanilla-unica/ventanilla-unica-contenido.model";
import { TipoContenidoModel } from "../../models/app-terrae/ventanilla-unica/tipo-contenido.model";
import { SearchPagination } from "../../interfaces/search.pagination";

@Injectable({
    providedIn: 'root'
})
export class VentanillaUnicaService {

    private urlBase = `${environment.backend.app_terrae}api/ventanilla-unica`;

    constructor(
        private http: HttpClient
    ) { }

    public findById(id: number): Observable<VentanillaUnicaItemModel> {
        return this.http.get<VentanillaUnicaItemModel>(`${this.urlBase}/` + id);
    }

    public findAllActivo(): Observable<VentanillaUnicaItemModel[]> {
        return this.http.get<VentanillaUnicaItemModel[]>(`${this.urlBase}/find-all-active`);
    }

    public findAllByPadre(padreId?: number): Observable<VentanillaUnicaFullModel> {
        const params = padreId != null ? { padreId: padreId.toString() } : {};
        return this.http.get<VentanillaUnicaFullModel>(`${this.urlBase}/find-all-by-padre`, { params });
    }

    public save(ventanillaUnicaItem: VentanillaUnicaItemModel): Observable<VentanillaUnicaItemModel> {
        return this.http.post<VentanillaUnicaItemModel>(`${this.urlBase}/save/item`, ventanillaUnicaItem);
    }

    public saveContenido(ventanillaUnicaContenido: VentanillaUnicaContenidoModel): Observable<VentanillaUnicaContenidoModel> {
        return this.http.post<VentanillaUnicaContenidoModel>(`${this.urlBase}/save/contenido`, ventanillaUnicaContenido);
    }

    public updateOrdenItems(listaOrden: VentanillaUnicaItemModel[], itemPadreId?: number): Observable<VentanillaUnicaItemModel[]> {
        const params = { params: itemPadreId != null ? { itemPadreId: itemPadreId.toString() } : {} };
        return this.http.put<VentanillaUnicaItemModel[]>(`${this.urlBase}/update/orden/item`, listaOrden, params);
    }

    public updateOrdenContenidos(listaOrden: VentanillaUnicaContenidoModel[], itemPadreId?: number): Observable<VentanillaUnicaContenidoModel[]> {
        const params = { params: itemPadreId != null ? { itemPadreId: itemPadreId.toString() } : {} };
        return this.http.put<VentanillaUnicaContenidoModel[]>(`${this.urlBase}/update/orden/contenidos`, listaOrden, params);
    }

    public findAllTipoContenido(): Observable<TipoContenidoModel[]> {
        return this.http.get<TipoContenidoModel[]>(`${environment.backend.app_terrae}api/tipo-contenido/all`);
    }

    public findContenidoBySearch(search: string, page: number, records: number): Observable<IPaginacion<VentanillaUnicaContenidoModel>> {
        return this.http.get<IPaginacion<VentanillaUnicaContenidoModel>>(`${this.urlBase}/buscar-contenido?search=${search}&page=${page}&records=${records}`);
    }

}