import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { VentanillaUnicaContenidoModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-contenido.model';
import { VentanillaUnicaFullModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-full.model';
import { VentanillaUnicaItemModel } from 'src/app/commons/models/app-terrae/ventanilla-unica/ventanilla-unica-item.model';
import { VentanillaUnicaService } from 'src/app/commons/services/app-terrae-services/ventanilla-unica.service';


// * Tipos de contenido * //
interface ContenidoMap {
  color: string;
  icono: string;
}

@Component({
  selector: 'app-ventanilla-unica',
  templateUrl: './ventanilla-unica.component.html',
  styleUrls: ['./ventanilla-unica.component.css']
})
export class VentanillaUnicaComponent implements OnInit, OnDestroy {

  // * Editar listas * //
  isEditableItems = false;
  isEditableContenidos = false;

  // * Lista con los items //
  public listadoItems: VentanillaUnicaItemModel[] = [];
  public listadoContenidos: VentanillaUnicaContenidoModel[] = [];
  public padre: VentanillaUnicaFullModel;
  public padreActual: VentanillaUnicaItemModel;
  public padreAnterior: VentanillaUnicaItemModel;

  // * Paginacin de la bsqueda * //
  public currentPage: number = 1;
  public records: number = 10;
  public totalElementos: number;
  public search: string = "";

  //* Otras * //
  public timeout: any = null;

  constructor(
    public ventanillaUnicaService: VentanillaUnicaService,
    private router: Router,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.listarPadres();
  }

  ngOnDestroy(): void {
    this.listadoItems = [];
  }

  // ***** EDITAR LISTAS ***** //
  dropItems(event: CdkDragDrop<VentanillaUnicaItemModel[]>) {
    moveItemInArray(this.listadoItems, event.previousIndex, event.currentIndex);
  }

  dropContenidos(event: CdkDragDrop<VentanillaUnicaContenidoModel[]>) {
    moveItemInArray(this.listadoContenidos, event.previousIndex, event.currentIndex);
  }

  toggleEditItems() {
    this.isEditableItems = !this.isEditableItems;
  }

  toggleEditContenidos() {
    this.isEditableContenidos = !this.isEditableContenidos;
  }

  // ****** LISTAR ITEMS Y CONTENIDOS ***** // 

  // & Guardamos el padre actual
  public guardarPadreActual(padre: VentanillaUnicaItemModel): void {
    this.padreActual = padre;
  }

  // & Listar todos los items padre
  public listarPadres(): void {
    this.ventanillaUnicaService.findAllByPadre().subscribe(result => {
      this.listadoItems = result.items;
      this.listadoContenidos = result.contenidos;
    })
  }

  // & Buscar items de un padre en concreto
  public listarHijos(id: number): void {
    this.ventanillaUnicaService.findAllByPadre(id).subscribe(result => {
      this.listadoItems = result.items;
      this.listadoContenidos = result.contenidos;
    })
  }

  // & Buscar ventanilla por id
  public buscarAbuelo(id: number): Observable<VentanillaUnicaItemModel> {
    return this.ventanillaUnicaService.findById(id).pipe(
      map(result => result)
    );
  }

  // & Contar cuantos items tiene un padre
  public contarHijos(id: number): Observable<number> {
    return this.ventanillaUnicaService.findAllByPadre(id).pipe(
      map(result => result.items.length)
    );
  }

  // & Contar cuantos contenidos tiene un item
  public contarContenidos(id: number): Observable<number> {
    return this.ventanillaUnicaService.findAllByPadre(id).pipe(
      map(result => result.contenidos.length)
    );
  }

  // & Asignar un cono un contenido
  public getIconoByTipo(tipo: string): string {
    switch (tipo) {
      case 'Texto': return 'ion ion-md-paper';
      case 'Telfono': return 'ion ion-md-call';
      case 'Correo': return 'ion ion-ios-mail';
      case 'Video': return 'ion ion-logo-youtube';
      case 'Link': return 'ion ion-ios-link';
      case 'PDF': return 'ion ion-md-document';
    }
  }

  // & Botn de atrs
  public listarPadreAnterior(): void {
    if (this.padreAnterior == null) {
      this.listarPadres();
    } else {
      this.buscarAbuelo(this.padreAnterior.id).subscribe(abuelo => {
        if (abuelo.padre && abuelo.padre.id) {
          this.padreAnterior = abuelo.padre;
          this.ventanillaUnicaService.findAllByPadre(this.padreAnterior.id).subscribe(result => {
            this.listadoItems = result.items;
          });
        } else {
          this.listarPadres();
        }
      });
    }
  }

  // & Guardar padre anterior, especialmente til cuando queremos volver atrs
  public guardarPadreAnterior(padre: VentanillaUnicaItemModel) {
    this.padreAnterior = padre;
  }

  // ***** BOTN CREAR NUEVO ITEM ***** //
  public crearItem(): void {
    this.router.navigate([`app-terrae/ventanilla-unica/new-item`]);
  }

  // ***** BOTN CREAR NUEVO CONTENIDO ***** //
  public crearContenido(): void {
    this.router.navigate([`app-terrae/ventanilla-unica/new-contenido`])
  }

  // ***** BOTN EDITAR ORDEN ***** //
  public actualizarOrdenItems(): void {
    this.isEditableItems = false;
    if (this.padreActual) {
      this.ventanillaUnicaService.updateOrdenItems(this.listadoItems, this.padreActual.id).subscribe(result => {
        this.listadoItems = result;
        this.cdr.detectChanges();
      })
    }
  }

  public actualizarOrdenContenidos(): void {
    this.isEditableContenidos = false;
    if (this.padreActual) {
      this.ventanillaUnicaService.updateOrdenContenidos(this.listadoContenidos, this.padreActual.id).subscribe(result => {
        this.listadoContenidos = result;
        this.cdr.detectChanges();
      })
    }
  }

  // ***** BARRA DE BSQUEDA ***** //

  // & Esto va a buscar los contenidos al servicio
  // public buscarContenidos(): void {
  //   this.ventanillaUnicaService.findContenidoBySearch(this.search, this.currentPage, this.records).subscribe(result => {
  //     this.listadoContenidos = result;
  //   })
  // }

  // & Esto acciona la bsqueda luego de 0.8s de no escribir
  public cambiarTextoBarraBusqueda(event: any) {
    clearTimeout(this.timeout);
    let $this = this;
    this.timeout = setTimeout(function () {
      if (event.keyCode != 13) {
        $this.cambiarSearch(event.target.value);
      }
    }, 800);
  }


  // & Esto busca los contenidos
  public cambiarSearch(busqueda: string): void {
    this.search = busqueda;
    this.currentPage = 1;
    this.listarPadres();
  }

}
